.bd-toc {
  position: sticky;
  top: 5rem;
  right: 0;
  z-index: 2;
  height: subtract(100vh, 7rem);
  overflow-y: auto;
  grid-area: toc;
}
.bd-sidebar {
  grid-area: sidebar;
}
.bd-main {
  grid-template-areas: "intro toc" "content toc";
  grid-template-columns: 4fr 1fr;
  grid-template-rows: auto 1fr;
  display: grid;
  gap: inherit;
  grid-area: main;
}
.bd-example {
  position: relative;
  padding: 1.5rem;
  margin-right: 0;
  margin-left: 0;
}
